import * as nacl from "tweetnacl";
import { sendAndConfirmRawTransaction } from "@solana/web3.js";

export async function signAndSend(connection, transaction, wallet, signers) {
    transaction.feePayer = wallet.publicKey;
    let blockhashObj = await connection.getLatestBlockhash();
    transaction.recentBlockhash = blockhashObj.blockhash;
    let signed = await wallet.signTransaction(transaction);
    let transactionBuffer = signed.serializeMessage();

    (signers || []).forEach((s) => {
        let signature = nacl.sign.detached(transactionBuffer, s.secretKey);
        signed.addSignature(s.publicKey, Buffer.from(signature));
        let isVerifiedSignature = signed.verifySignatures();
        console.log(`The signatures were verifed: ${isVerifiedSignature}`);
    });

    let rawTransaction = signed.serialize();
    try {
        const tx=  await sendAndConfirmRawTransaction(connection, rawTransaction);
        await connection.confirmTransaction(tx)
    } catch (error) {
        debugger
        console.error(error)
    }
}
